import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { CSSTransition, SwitchTransition } from 'react-transition-group';

import Navbar from './components/layout/Navbar';
import ScrollToTop from './components/layout/ScrollToTop';

import Home from './components/pages/Home';
import DotNetCore from './components/pages/DotNetCore';
import Embedded from './components/pages/Embedded';
import Contacts from './components/pages/Contacts';

import './App.css';

const routes = [
  { path: '/m-faried-portfolio/dot-net', toComponent: DotNetCore },
  { path: '/m-faried-portfolio/embedded', toComponent: Embedded },
  { path: '/m-faried-portfolio/contacts', toComponent: Contacts },
  { path: '/m-faried-portfolio', toComponent: Home },
];

const App = () => {
  return (
    <Router>
      <ScrollToTop />
      <div className='App'>
        <Navbar />
        <Route
          render={({ location }) => {
            return (
              <SwitchTransition mode='out-in'>
                <CSSTransition
                  classNames='fade'
                  key={location.key}
                  addEndListener={(node, done) =>
                    node.addEventListener('transitionend', done, false)
                  }
                >
                  <Switch location={location}>
                    {routes.map(({ path, toComponent }) => (
                      <Route
                        exact
                        key={path}
                        path={path}
                        component={toComponent}
                      />
                    ))}
                  </Switch>
                </CSSTransition>
              </SwitchTransition>
            );
          }}
        />
      </div>
    </Router>
  );
};

export default App;
