import React from 'react';
import './DotNetCore.css';

const linkStyle = {
  color: '#61dbfb',
};

const Embedded = () => {
  return (
    <div className='container my-5'>
      <div className='field-container'>
        <div className='field-logo'>
          <img
            src={`${process.env.PUBLIC_URL}/images/logo-embedded.png`}
            alt='embedded'
          />
          <h1>Embedded Development</h1>
          <a
            style={linkStyle}
            href='https://www.linkedin.com/in/mohamed-faried-0258a445/'
            target='blank'
          >
            Check Linked IN
          </a>
        </div>

        <div className='field-desc'>
          <p className=' my-4'>
            Embedded software developer using Model Based Development approach
            for autmotive control systems for 4 Years @ Valeo Automotive Systems
          </p>
        </div>
      </div>
    </div>
  );
};

export default Embedded;
