import React, { Fragment } from 'react';
import './Intro.css';

const Intro = () => {
  const logogSrc = process.env.PUBLIC_URL + '/images/logo.jpg';
  // const backgroundURL = process.env.PUBLIC_URL + '/images/react.svg';

  return (
    <Fragment>
      <div className='container my-5'>
        <div className='intro-container'>
          {/* <div className="bg-img" style={{ backgroundImage: `url()` }}></div> */}

          <div className='logo'>
            <img src={logogSrc} alt='' />
          </div>

          <div className='intro-text'>
            <h2>
              He<span className='text-blue'>ll</span>o,
            </h2>

            <h2>
              <span className='text-blue'>I</span> am
            </h2>

            <h1>
              M<span className='text-blue'>.</span>FAR
              <span className='text-blue'>I</span>ED
            </h1>

            <small className='text-blue'>
              Full Stack Web Developer / Desktop Developer / Freelancer
            </small>
          </div>
        </div>
      </div>

      <div className='container my-5'>
        <div className='pitch-container'>
          A passionate software engineer who has more than{' '}
          <span className='text-blue'>10 years of professional experience</span>{' '}
          in various software development technologies.
        </div>
      </div>
    </Fragment>
  );
};

export default Intro;
