import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';

import './Navbar.css';

const Navbar = () => {
  const [open, setOpen] = useState(false);

  return (
    <nav className={open ? 'show' : ''}>
      <div className='nav-container'>
        {/*  */}

        {/* Home Page Link */}
        <NavLink to='/m-faried-portfolio' onClick={() => setOpen(false)}>
          <i className='fas fa-home fa-2x'></i>
        </NavLink>

        {/* Contacts Page Link */}
        <NavLink
          to='/m-faried-portfolio/contacts'
          onClick={() => setOpen(false)}
        >
          <i className='fas fa-address-card fa-2x'></i>
          <small>Contacts</small>
        </NavLink>

        {/* Linked In Link */}
        <a
          href='https://www.linkedin.com/in/mohamed-faried-0258a445/'
          target='blank'
        >
          <img
            src={`${process.env.PUBLIC_URL}/images/linkedin.svg`}
            alt='Linked In'
          />
        </a>

        {/* Github Link */}
        <a href='https://github.com/M-Faried' target='blank'>
          <img
            src={`${process.env.PUBLIC_URL}/images/github.svg`}
            alt='Linked In'
          />
        </a>

        {/* Blog */}
        <a href='https://mfariedtech.wordpress.com/' target='blank'>
          <img
            src={`${process.env.PUBLIC_URL}/images/wordpress-blue.svg`}
            alt='blog'
          />
        </a>
      </div>

      <button className='menu-btn' onClick={() => setOpen(!open)}>
        <i
          className={
            open ? 'fas fa-chevron-circle-left' : 'fas fa-chevron-circle-right'
          }
        ></i>
      </button>
    </nav>
  );
};

export default Navbar;
